// Для импорта в диспетчер подключений: @import './src/less/blocks/promo-info.less';


.promo-info {
  	background: url(/img/bg-opac.png)no-repeat;
	background-size: contain;
	background-position: 50% 50%;
	margin: auto;
	padding-bottom: @padding-std * 5;
	display: flex;
	flex-direction: column;
	justify-content: center;

	@media (max-width: @screen-xs) {
		background-size: cover;
	}

	@media (min-width: @screen-sm) {
		max-width: @screen-md;
	}

	&__title {
		font-family: @font-family-h;
		font-size: @font-size-h2;
		margin-bottom: @margin-bt;
		text-align: center;
		
	}

	&__text {
		font-family: @font-family-p;
		font-size: @font-size-p;
		padding: 0 @padding-std * 4;
		text-align: center;
		margin-bottom: @margin-bt;
		

		// @media (min-width) {
			
		// }
	}
}
