// Для импорта в диспетчер подключений: @import './src/less/blocks/promo-gallery.less';


.promo-gallery {
	margin: auto auto @margin-card;

	@media (min-width: @screen-lg) {
		max-width: @screen-lg;
	}

	

	&__pic {

		@media (max-width: @screen-xs) {
		display: none;
		
	}

		img {
			width: 100%;
			height: 100%;
		}

	}

	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
		margin-top: -3px;
		position: relative;
		background: @bg-dark-color;
		

		@media (max-width: @screen-xs) {
			display: none;
		}
	}

	&__item {
		
	}

	&__link {

		img {
			opacity: 0.5;

			@media (max-width: @screen-xs) {
				width: 100%;
				opacity: 1;
			}

			&:hover {
				opacity: 1;
			}

		}

	}
  
}
