// Для импорта в диспетчер подключений: @import './src/less/blocks/conditions-list.less';


.conditions-list {
		margin-bottom: 50px;	

	@media 	(min-width: @screen-xs) {
		flex-basis: 50%;	
	}
	
	&__item {
		padding-left: 20px;	
		position: relative;
		
		&:before {
			position: absolute;
			content:'';
			background: url(../img/sprite.png)no-repeat;
			background-position: -5px -58px;
			width: 10px;
			height: 15px;
			left: 0;
		}
	}
}
