.stitches-ic(@x: 0, @y: 0, @width: 0, @height: 0) {
    background-position: @x @y;
    width: @width;
    height: @height;
}

.ic {
    background-image: url(../img/sprite.png);    background-repeat: no-repeat;
    display: block;

    &.ic-arrow {
        .stitches-ic(-5px, -5px, 34px, 43px);
    }

    &.ic-arrow-item {
        .stitches-ic(-5px, -58px, 10px, 15px);
    }

    &.ic-fb-blue {
        .stitches-ic(-5px, -83px, 21px, 21px);
    }

    &.ic-i-bath {
        .stitches-ic(-5px, -114px, 52px, 28px);
    }

    &.ic-i-bed {
        .stitches-ic(-5px, -152px, 55px, 43px);
    }

    &.ic-i-bus {
        .stitches-ic(-5px, -205px, 57px, 34px);
    }

    &.ic-i-check {
        .stitches-ic(-5px, -249px, 16px, 12px);
    }

    &.ic-i-cottage {
        .stitches-ic(-5px, -271px, 80px, 62px);
    }

    &.ic-i-hotell {
        .stitches-ic(-5px, -343px, 82px, 64px);
    }

    &.ic-i-question {
        .stitches-ic(-5px, -417px, 16px, 17px);
    }

    &.ic-i-rooms {
        .stitches-ic(-5px, -444px, 27px, 43px);
    }

    &.ic-i-s {
        .stitches-ic(-5px, -497px, 49px, 49px);
    }

    &.ic-i-warning {
        .stitches-ic(-5px, -556px, 13px, 12px);
    }

    &.ic-i-wifi {
        .stitches-ic(-5px, -578px, 50px, 42px);
    }

    &.ic-icon-calendar {
        .stitches-ic(-5px, -630px, 15px, 16px);
    }

    &.ic-o-elect {
        .stitches-ic(-5px, -656px, 63px, 32px);
    }

    &.ic-vk-blue {
        .stitches-ic(-5px, -698px, 24px, 18px);
    }
}
