// Для импорта в диспетчер подключений: @import './src/less/blocks/menu.less';


.menu {
  	display: flex;	
	flex-direction: column;

	h2 {
	font-size: @font-size-h2;
	font-family: @font-family;
	margin-bottom: @margin-bt;
	}
	&__item {
		display: flex;	
		justify-content: flex-start;
		align-items: flex-start;
		margin-bottom: 	@margin-bt;

		span {
			margin-right: 100px;	
			flex-basis: 10%;
		}
	}
}
