// Для импорта в диспетчер подключений: @import './src/less/blocks/promo-slider.less';


.promo-slider {
	display: flex;
	flex-direction: column;

	&__item {
		display: none;
		margin-bottom: @margin-std * 2;
		min-height: 400px;

		&--visible {
			display: block;
			
		}

		&--slider1 {
			background: url(/img/slider1.jpg)no-repeat;
			background-size: cover;
			background-position: 50% 50%;

			@media (max-width: @screen-sm) {
				background: url(/img/slider1-768.jpg)no-repeat;
				background-size: cover;
			}

			@media (max-width: @screen-xs) {
				background: url(/img/slider1-320.jpg)no-repeat;
				background-size: cover;
			}
		}

		&--slider2 {
			background: url(/img/slider2.jpg)no-repeat;
			background-size: cover;
			background-position: 50% 50%;

			@media (max-width: @screen-sm) {
				background: url(/img/slider2-768.jpg)no-repeat;
				background-size: cover;
			}

			@media (max-width: @screen-xs) {
				background: url(/img/slider2-320.jpg)no-repeat;
				background-size: cover;
			}
		}


	}

	&__wrap {
		padding-top: 20px;
		max-width: @screen-lg;
		

		@media (max-width: @screen-xs) {
			background: @bg-slider-text;
			width: 100%;
			text-align: center;
			padding: 10px 20px;
			margin-top: 20px;
			
		}

		@media (min-width: @screen-xs) {
			padding-left: 20px;
			width: 65%;
		}

		@media (min-width: @screen-lg) {
			padding: 100px;
			width: 55%;
		}

	}

	&__title {
		font-family: @font-family-h;
		font-size: @font-size-h1;
		background: @bg-slider-text;
		display: inline;
		

		@media (max-width: @screen-xs) {
			font-size: @font-size-accent;
			display: block;
			
		}
	}

	&__text {
		font-family: @font-family-p;
		margin-top: 20px;

		p {
			background: @bg-slider-text;
			display: inline;
			line-height: 24px;
		}
		
	}

}
