// Для импорта в диспетчер подключений: @import './src/less/blocks/date-stay.less';


.date-stay {
	display: flex;
	justify-content: space-around;
	align-items: center;
	background:@bg-white;
	max-width: @screen-xs;
	margin: auto;

	@media (max-width: @screen-xs) {
		flex-direction: column;
		background: none;
	}


	&__item {
		padding: 0px 30px;
		margin: 30px 0px;
		font-size: @font-size;
		font-family: @font-family-p;
		text-transform: uppercase;
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		align-items: center;
		max-width: @screen-xxs;

		&:first-child {
			border-right: 1px solid @bg-light-grey;

			@media (max-width: @screen-xs) {
				border: none;
			}
		}

	}

	&__title {
		margin-bottom: 10px;


	}

	&__input {
		margin-top: 20px;
	}

	&__date {
		text-align: center;
	    border: 1px solid #eee;
	    padding: 10px;
	    color: #0ab89d;
	    letter-spacing: 0.1em;
	    cursor: pointer;
	    font-weight: 500;

	}

	
}
