// Для импорта в диспетчер подключений: @import './src/less/blocks/booking-steps.less';


.booking-steps {

	&__inner {
		display: flex;
		flex-direction: column;
		justify-content: center;
		text-align: center;
		margin: auto;
		padding: @padding-std * 5 0px;
		max-width: @screen-sm;

	}

	&__step {
		font-size: @font-size;
		font-family: @font-family-p;
		color: @bg-grey-step;
		text-transform: uppercase;
		margin-bottom: @margin-bt / 2;
	}

	&__title {
		font-size: @font-size-h3;
		font-family: @font-family-h;
		text-align: center;
		margin-bottom: @margin-bt;

	}

	&__btn {
		padding: @padding-std *2 @padding-std * 8;
		text-align: center;
		border: none;
		margin: 0;
		letter-spacing: @letter-spacing;
		cursor: pointer;



		@media (max-width: @screen-xs) {
			width: auto;			
		}

		@media (max-width: @screen-sm) {
			width: 100%;			
		}

		@media (min-width: @screen-sm) {
			width: 100%;			
		}
	}

	&--bg-grey {
		background: @bg-grey-booking;
	}

  
}
