// Для импорта в диспетчер подключений: @import './src/less/blocks/user-info.less';


.user-info {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	flex-direction: row;


	@media (max-width: @screen-xs) {
		flex-direction: column;
	}


	&__label {
		display: flex;
		flex-wrap: wrap;
		flex-direction: row;
		flex-basis: 50%;
		padding: 0px @padding-std;
		margin-bottom: @margin-std;
		position: relative;
	}

	&__input {
		padding: @padding-std * 2 @padding-std *6;
		margin-bottom: @margin-std;
		text-align: center;
		text-transform: uppercase;
		border: 1px solid @bg-light-grey;
		position: relative;
		width: 100%;
		
		transition: background-color 1s ease;

		&:focus::-webkit-input-placeholder {
			color: transparent;
		}

		&:required:valid {
			background: url(data:image/svg+xml;base64,);
			background-repeat: no-repeat;	
			background-position: 5% 50%;
		}

	}

	&__doptext {
		flex-basis: 100%;
		font-family: @font-family-p;
		font-size: @font-size;
		text-align: left;
		letter-spacing: @letter-spacing;
	}
}
