// Для импорта в диспетчер подключений: @import './src/less/blocks/control-group.less';


.control-group {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-bottom: @margin-card;

	&__item {
		padding: 0 10px;
		text-transform: uppercase;
		font-size: @font-size;
		font-family: @font-family-p;
		position: relative;
		cursor: pointer;
		

		&--active {
			color: @bg-orange;
			position: relative;

				&:before {
				position: absolute;
				content: '';
				width: 30px;
				height: 2px;
				background: @bg-orange;
				bottom: -5px;
				left: 50%;
				transform: translate(-50%, -50%);
			}
		}
	}
}
