// Для импорта в диспетчер подключений: @import './src/less/blocks/content-list.less';


.content-list {
	display: flex;
	justify-content: space-around;
	margin: auto;
	padding: 30px;
	max-width: 100%;
	background:@bg-white;
	border-bottom: 1px solid @bg-light-grey;
	border-top: 1px solid @bg-light-grey;
	position: relative;
	
	&:before {
		position: absolute;
		content: '';
		width: 30px;
	    height: 20px;
	    top: -19px;
	    left: 85px;
		background-image: url(data:image/svg+xml;base64,);
	}

	@media (max-width: @screen-xs) {
		display: none;
		}

	&__item {
		padding-top: 55px;
		color: @bg-dark-color;
		font-size: @font-size;
		font-family: @font-family-p;
		letter-spacing: @letter-spacing;
		text-transform: uppercase;
		flex-basis: 16%; 
		text-align: center;
		
		

		&--pic1 {
			position: relative;

			&:before {
				position: absolute;
				content: '';
				width: 49px;
				height: 49px;
				top: 0;
				left: 50%;
				transform: translatex(-50%);
				background: url(../img/sprite.png)no-repeat;
				background-position: -5px -497px;
				
			}

		}

		&--pic2 {
			position: relative;

			&:before {
				position: absolute;
				content: '';
				width: 27px;
				height: 43px;
				top: 0;
				left: 50%;
				transform: translatex(-50%);
				background: url(../img/sprite.png)no-repeat;
				background-position: -5px -444px;
			}

		}

		&--pic3 {
			position: relative;

			&:before {
				position: absolute;
				content: '';
				width: 55px;
				height: 43px;
				top: 0;
				left: 50%;
				transform: translatex(-50%);
				background: url(../img/sprite.png)no-repeat;
				background-position: -5px -152px;
			}

		}

		&--pic4 {
			position: relative;

			&:before {
				position: absolute;
				content: '';
				width: 52px;
				height: 28px;
				top: 0;
				left: 50%;
				transform: translatex(-50%);
				background: url(../img/sprite.png)no-repeat;
				background-position: -5px -114px;
			}

		}

		&--pic5 {
			position: relative;

			&:before {
				position: absolute;
				content: '';
				width: 50px;
				height: 42px;
				top: 0;
				left: 50%;
				transform: translatex(-50%);
				background: url(../img/sprite.png)no-repeat;
				background-position: -5px -578px;
			}

		}
		
	}
}
