// Для импорта в диспетчер подключений: @import './src/less/blocks/payment-card.less';


.payment-card {
	padding-top: 26px;
	text-transform: uppercase;
	display: flex;
	justify-content: space-around;
	align-items: flex-start;

	@media (max-width: @screen-xs) {
		flex-direction: column;
	}

	&__item {

		&:last-child {
			flex-basis: 30%;
			flex-shrink: 1;
		}

	}



	&__label {
		display: flex;
		flex-direction: column;
		flex-basis: 50%;
		padding: 0px @padding-std;
		margin-bottom: @margin-std;
	}

	&__input {
		padding: @padding-std * 2;
		margin-bottom: @margin-std;
		text-align: center;
		text-transform: uppercase;
		border: 1px solid @bg-light-grey;

		&:focus::-webkit-input-placeholder {
			color: transparent
		}

	}
}
