// Для импорта в диспетчер подключений: @import './src/less/blocks/reviews.less';


.reviews {
	background: url(/img/reviews-bg.jpg)no-repeat;
	background-size: cover;
	background-position: 50% 50%;
	padding-top: @padding-std * 7;
	font-family: @font-family-p;
	margin-bottom: @margin-bt + 10px;

	&__inner {
		display: flex;
		justify-content: space-around;
		align-items: center;
		max-width: @screen-lg;
		margin: auto;
		position: relative;
		
		@media (max-width: @screen-sm) {
			flex-direction: column;
		}
	}

	&__title {
		text-align: center;
		margin: auto;
		font-size: @font-size-h3;
		font-family: @font-family-h;
		color: @text-white;
		margin-bottom: @margin-std * 6;

	}

	&__item {
		text-align: center;
		padding: 0 @padding-std * 2;
		align-self: stretch;
		display: flex;
		flex-direction: column;
		margin-bottom: @margin-bt;
		position: relative;
		

		@media (max-width: @screen-xs) {
			padding: 0;
			margin-bottom: 0;
		}

		@media (max-width: @screen-sm) {
			max-width: @screen-sm;
			margin: auto auto @margin-card;
		}

	}

	&__wrap {
		position: relative;
		padding: 50px 0px 30px;
		align-self: stretch;

		@media (max-width: @screen-xs) {
			padding-left: 0;
			padding-right: 0;
		}

		@media (max-width: @screen-sm) {
			padding-left: 20px;
			padding-right: 20px;
		}

		&--bg {
			background: @bg-white; 
		}

	}


	&__users {

	}

	&__pic {
		max-width: 80px;
		height: auto;
		margin: auto;
		position: absolute;
	    z-index: 5;
	    top: -30px;
	    left: 0;
	    right: 0;
	    margin: 0 auto;
			


		img {
			border: 1px solid @text-white;
		}
	}

	&__name {

	}

	&__link {

		&--social {
			margin: auto;
			font-size: 0;

		}

	}

	&__text {
		font-size: @font-size-p;
		margin-bottom: @margin-std * 2;
		    padding: 0  @padding-std;

	}

	&__date {
		font-size: @font-size;
	}

	&__btn {
		margin: auto;
		padding-bottom: @padding-std * 5;
		text-align: center;
		

	}

}
