// Для импорта в диспетчер подключений: @import './src/less/blocks/main-nav.less';


.main-nav {
	font-family: @font-family;
	font-size: 	@font-size;
	padding: 0;
	margin: 0;
	position: absolute;
	z-index: 2;
	top: 98px;
	right: 0;
	left: 0;
	display: none;
  
	&--visible {
		display: block;
	}


	@media (min-width: @screen-md) {
	    position: static;
	    display: flex;
	    flex-wrap: wrap;
	  }
			
	&__list {
		display: flex;	
		flex-wrap: wrap;
		background: @text-white;
		letter-spacing: @letter-spacing;


		@media (max-width: @screen-md) {
			flex-direction: column;	
			
		}
	}

	&__item {
		align-self: center;

		&--active {
			position: relative;
			
			&:before {
				position: absolute;
				content: '';
				width: 30px;
				height: 2px;
				background: @bg-orange;
				bottom: 0;
				left: 50%;
				transform: translate(-50%, -50%);


				@media (max-width: @screen-sm) {
					background: transparent;
				}
			}
		}

		&--active-border {
			position: relative;
			

			&:after {
				position: absolute;
				content: '';
				width: 30px;
				height: 2px;
				background: @bg-orange;
				bottom: -5px;
				left: 50%;
				transform: translate(-50%, -50%);


				@media (max-width: @screen-sm) {
					background: transparent;
				}
			}

		}

		@media (max-width: @screen-sm) {
			border-bottom: 1px solid @border-color;
			width: 100%;
			text-align: center;

			&:first-child {
				border-top: 1px solid @border-color;
			}


		}
	}

	&__link {
		padding: @padding-std 5px;
		text-transform: uppercase;
		color: @text-color;
		position: relative;

		&:hover,
		&:active {
			color: @bg-orange-hover;
		}

		&:focus:before {
			position: absolute;
				content: '';
				width: 30px;
				height: 2px;
				background: @bg-orange-hover;
				bottom: 0;
				left: 50%;
				transform: translate(-50%, -50%);

			// @media 	(max-width: @screen-md) {
   //  			left: 45%;
			// }

			@media (max-width: @screen-sm) {
				position: static;
			}
		}

	}
	
}
