// Для импорта в диспетчер подключений: @import './src/less/blocks/payment.less';


.payment {
	font-family: @font-family-p;
	font-size: @font-size;
	letter-spacing: @letter-spacing;
	color: @bg-grey;
	margin-bottom: @margin-std * 3;
	text-align: left;

	&__inner {
		display: flex;
		justify-content: space-around;

		@media (min-width: @screen-xxs) {
			flex-direction: column;
		}

		@media (min-width: @screen-sm) {
			flex-direction: row;
		}

	}

	&__item {
		margin: 0px @margin-std @margin-std *2;
		background: @bg-white;
		border: 1px solid @bg-light-grey;

		@media (max-width: 767px) {
			margin-left: 0;
			margin-right: 0;
		}

		&:first-child {
			margin-left: 0;
			flex-basis: 45%;
			flex-shrink: 1;


			@media (max-width: @screen-xs) {
				order: 1;
			}
		}

		&:last-child {
			margin-right: 0;
		}

		&--bgr {
			background: none;
		}

	}

	&__caption {
		background: @bg-white;
		padding: 25px @padding-std2;
	}

	&__name {
		text-transform: uppercase;
		font-weight: 500;
		text-align: center;
		

	}

	&__price {
		color: @bg-dark-color;
		text-align: center;
		

	}

	&__warning {

	}

	&__arrival {
		background: @bg-white;
		padding: @padding-std;
		margin-bottom: @margin-std;

		@media (max-width: @screen-xs) {
			padding: @padding-std @padding-std*4;
		}
	}

	&__now {
		background: @bg-white;
		padding: @padding-std2;

		@media (max-width: @screen-xs) {
			padding: @padding-std2 * 2;
		}

	}
  
}
