// Для импорта в диспетчер подключений: @import './src/less/blocks/shares-slider.less';


.shares-slider {
	margin-bottom: @margin-std * 2;

	&__item {
		min-height: 370px;		

		&--shares1 {
			background: url(/img/main-shares.jpg)no-repeat;
			background-size: cover;
			background-position: 50% -53%;

			@media (max-width: @screen-sm) {
				background: url(/img/main-shares-768.jpg)no-repeat;
				background-size: cover;
			}

			@media (max-width: @screen-xs) {
				background: url(/img/main-shares-320.jpg)no-repeat;
				background-size: cover;
			}
		}

		&--shares2 {
			background: url(/img/slider2.jpg)no-repeat;
			background-size: cover;
			background-position: 50% -53%;

			@media (max-width: @screen-sm) {
				background: url(/img/slider2-768.jpg)no-repeat;
				background-size: cover;
			}

			@media (max-width: @screen-xs) {
				background: url(/img/slider2-320.jpg)no-repeat;
				background-size: cover;
			}
			
		}
	}

	&__wrap {
		padding: 200px 20px 20px 0px;
		margin-bottom: 30px;

	}

	&__text {
		padding: 20px;
		background: rgba(255,255,255,.9);
		width: 40%;
		font-family: @font-family-p;
		font-size: @font-size-p;

		@media (max-width: @screen-xs) {
			width: 100%;
		}

		@media (min-width: @screen-md) {
			width: 30%;
		}

		p {
			text-transform: uppercase;
			color: @bg-orange;
			margin-bottom: @margin-std;
			font-weight: 500;
		}
	}

	&__btn {
		display: flex;
		justify-content: center;
		align-items: center;
		position: relative;
		padding: 20px;
	}

	&__control {
		width: 13px;
		height: 13px;
		border-radius: 50%;
		border: 2px solid @bg-orange;

		&:first-child {
			margin-right: @margin-std * 2;
		}
		

		&--active {
			background: @bg-orange;
			
				
		}

	}
  
}
