// Для импорта в диспетчер подключений: @import './src/less/blocks/categories.less';


.categories {
	max-width: @screen-lg;
	margin: auto;

	&__inner {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;

	}

	&__title {
		font-size: @font-size-h2;
		font-family: @font-family-h;
		margin: @margin-card 0px @margin-bt;

	}

	&__item {
		background:@bg-light-color;
		border: 1px solid @bg-light-grey;
		margin-bottom: @margin-std * 4;
		width: 100%;
		overflow: hidden;
		

	}
}
