// Для импорта в диспетчер подключений: @import './src/less/blocks/category.less';


.category {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	position: relative;

	@media (max-width: @screen-sm) {
		flex-direction: column;
	}


	&__title {
		margin: @margin-bt 0 @margin-bt - 10px;
		text-align: center;

		@media (min-width: @screen-md) {
			align-self: flex-start;
			// padding: 0 @padding-std * 15;
			width: 44%;
		}
		

		h3 {
		font-family: @font-family-h;
		font-size: @font-size-h3;
		}
		
	}

	&__price {
		font-size: @font-size;
		font-family: @font-family-p;
		text-transform: uppercase;	
		font-weight: 500;

		&--orange {
			color: @bg-orange;
		}
	}

	&__pic {
		margin-bottom: @margin-bt;

		@media (min-width: @screen-sm) {
			padding: 0 @padding-std *7;
		}

		@media (min-width: @screen-md) {
			padding: 0;
			position: absolute;
			top: 0;
			right: 0;
			width: 50%;
			
		}

		img {
			width: 100%;
			height: 100%;
		}

	}

	&__info {
		padding: 0px @padding-std * 2;
		font-family: @font-family-p;
		font-size: @font-size-p;

		@media (min-width: @screen-md) {
			align-self: flex-start;
			margin-left: 5%;
			width: 40%;
		}

	}

	&__text {
		margin-bottom: @margin-std *4;

		@media (min-width: @screen-sm) {
			max-width: @screen-xs;
		}
	}

	&__link {
		display: flex;
		justify-content: space-around;
		align-items: center;
		margin-bottom: @margin-std *4;

		@media (max-width: @screen-xs) {
			flex-direction: column;
		}

		@media (min-width: @screen-md) {
			justify-content: space-between;
		}

	}
}
