.pickmeup {
    background: #fff;
    border-radius: none;
    -moz-box-sizing: content-box;
    box-sizing: content-box;
    display: inline-block;
    position: absolute;
    border: 1px solid #eee;
    touch-action: manipulation
}

.pickmeup * {
    -moz-box-sizing: border-box;
    box-sizing: border-box
}

.pickmeup.pmu-flat {
    position: relative
}

.pickmeup.pmu-hidden {
    display: none
}

.pickmeup .pmu-instance {
    display: inline-block;
    height: 13.8em;
    padding: 0;
    text-align: center;
    width: 15em
}

.pickmeup .pmu-instance .pmu-button {
    color: #191919;
    cursor: pointer;
    outline: 0;
    text-decoration: none
}

.pickmeup .pmu-instance .pmu-today {
    background: rgba(10, 184, 157, 0.61);
    color: #fff
}

.pickmeup .pmu-instance .pmu-button:hover {
    background: 0 0;
    // color: #fff
}

.pickmeup .pmu-instance .pmu-not-in-month {
    color: #666
}

.pickmeup .pmu-instance .pmu-disabled,.pickmeup .pmu-instance .pmu-disabled:hover {
    color: #333;
    cursor: default
}

.pickmeup .pmu-instance .pmu-selected {
    background: #0ab89d;
    color: #fff
}

.pickmeup .pmu-instance .pmu-not-in-month.pmu-selected {
    background: #17384d
}

.pickmeup .pmu-instance nav {
    color: #eee;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    line-height: 2em;
    background: #ff7435;
    border-bottom: 1px solid #fff;
}

.pickmeup .pmu-instance nav :first-child :hover {
    color: #88c5eb
}

.pickmeup .pmu-instance nav .pmu-next,.pickmeup .pmu-instance nav .pmu-prev {
    display: none;
    height: auto;
}

.pickmeup .pmu-instance nav .pmu-month {
    width: 14em;
    color: #fff;
}

.pickmeup .pmu-instance .pmu-months *,.pickmeup .pmu-instance .pmu-years * {
    display: inline-block;
    line-height: 3.6em;
    width: 3.5em
}

.pickmeup .pmu-instance .pmu-day-of-week {
    color: #fff;
    cursor: default
}

.pickmeup .pmu-instance .pmu-day-of-week *,.pickmeup .pmu-instance .pmu-days * {
    display: inline-block;
    line-height: 1.5em;
    width: 2em
}

.pickmeup .pmu-instance .pmu-day-of-week * {
    line-height: 1.8em
}

.pickmeup .pmu-instance:first-child .pmu-prev,.pickmeup .pmu-instance:last-child .pmu-next {
    display: block
}

.pickmeup .pmu-instance:first-child .pmu-month,.pickmeup .pmu-instance:last-child .pmu-month {
    width: 13em
}

.pickmeup .pmu-instance:first-child:last-child .pmu-month {
    width: 12em
}

.pickmeup:not(.pmu-view-days) .pmu-day-of-week,.pickmeup:not(.pmu-view-days) .pmu-days,.pickmeup:not(.pmu-view-months) .pmu-months,.pickmeup:not(.pmu-view-years) .pmu-years {
    display: none
}

.pmu-prev.pmu-button {
    border-right: 1px solid #fff;
    border-left: none
}

.pmu-next.pmu-button {
    border-left: 1px solid #fff
}
