// Для импорта в диспетчер подключений: @import './src/less/blocks/main-colors.less';


.main-colors {
	display: flex;	

	&__inner {
		display: flex;	
		justify-content: flex-end;
		flex-wrap: wrap;

	}

	&__item {
		width: 110px;
		height: 96px;	
		margin:0px 20px;	
		display: flex;	
		flex-direction: column;	
		justify-content: flex-end;
		text-align: center;
		border: 1px solid @border-color;
	}

	&__name {
		background-color: @text-white;
		font-size: @font-size-accent - 3;
		padding: 10px 30px 10px 25px;
	}

	&--orange {
		background: @bg-orange;	
	}

	&--orange-hov {
		background: @bg-orange-hover;	
	}

	&--green {
		background: @bg-green;	
	}
  
}
