// Для импорта в диспетчер подключений: @import './src/less/blocks/rest-list.less';


.rest-list {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	margin-bottom: @margin-card;
	

	@media (max-width: @screen-xs) {
		display: none;
		
	}

	@media (max-width: @screen-sm) {
		margin: auto;
		
	}

	&__item {
		font-family: @font-family-p;
		font-size: @font-size;
		text-transform: uppercase;
		font-weight: 500;
		text-align: center;
		padding-top: 90px;
		width: 100% / 6;

		&--pic1 {
			position: relative;

			&:before {
			position: absolute;
			content: '';
			width: 80px;
			height: 80px;
			border-radius: 50%;
			background: url(/img/rest-list-swimming.jpg)no-repeat;
			background-size: cover;
			background-position: 50% 50%;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			margin: 0 auto;
			}
		}

