.page-header {
	color: @text-color;
	border-bottom: 1px solid @border-color;
	
	&__inner {
		position: relative;
		top: 0;
		right: 0;
		left: 0;
		display: flex;
		justify-content: space-around;
    	align-items: center;
    	margin: auto;
    	max-width: @screen-lg;

    	@media (min-width: @screen-sm) {
    		position: static;
    		height: auto;
    	}

		@media (max-width: @screen-sm) {
			background: @text-white;
			border: 1px solid @border-color;
			flex-direction: column;
			align-items: stretch;	
		}
	}
	

	@media (min-width: @screen-sm) {
	    position: static;
	    height: auto;
	  }


	&__contacts-and-book {
		display: flex;	

		@media 	(max-width: @screen-md) {
			display: none;
		}

	}
	
	&__phone {
		font-size: 	@font-size-accent;
		font-family: @font-family-p;
		color: @text-color;
		font-weight: 500;	
	}
	
	&__adress {
		font-size: 	@font-size;
	}
}
