// Для импорта в диспетчер подключений: @import './src/less/blocks/offers.less';


.offers {
	background: @bg-light-grey;
	padding: @padding-std * 5 @padding-std2;	

		
	&__inner {
		display: flex;	
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
		max-width: @screen-lg;
		margin: auto;

		@media 	(min-width: @screen-xxs) {
			flex-direction: column;	
			
		}

		@media 	(min-width: @screen-xs) {
			flex-direction: row;	
			
		}
	}

	&__item {
		padding: 0 @padding-std;	

	    @media (min-width: @screen-xs) {
			flex-basis: 50%;
	    }

	    @media (min-width: @screen-md) {
			flex-basis: 30%;
	    }

	}

	&__wrap {
		border: 1px solid @border-color;
		background: @bg-light-color;
	    padding: 0px 0px @padding-std 0px;
	    display: flex;
	    flex-direction: column;
	    justify-content: center;
	    align-items: center;
	    margin-bottom: @margin-std2;
	    text-align: center;
	    color: @text-color;

	}

	&__pic {
		width: 100%;

		img {
			width: 100%;
		}
	}

	&__info {
		padding: 40px 0px 50px 0px;	
	}

	&__title {
		font-family: @font-family-h;
		font-size: 	@font-size-h3;
		margin-bottom: @margin-std;
		letter-spacing: @letter-sp-title; 
	}

	&__price {
		font-size: @font-size-accent;
		font-family: @font-family-p;
		text-transform: uppercase;	
		font-weight: 500;

		&--orange {
			color: @bg-orange;
		}
	}
}
