// Для импорта в диспетчер подключений: @import './src/less/blocks/payment-caption.less';


.payment-caption {
	display: flex;
	align-items: center;
	justify-content: flex-end;


	&__name {
		flex-shrink: 1;
		flex-basis: 35%;
		padding-left: 37px;
		text-transform: uppercase;
		font-weight: 500;
		color: @bg-dark-color;

		@media (max-width: @screen-xs) {
			flex-basis: 100%;
			padding-top: 13px;
			padding-bottom: 13px;
			
		}

		&--active {
			position: relative;

			&:before {
				position: absolute;
				content: '';
				background-image: url(data:image/svg+xml;base64,);
				background-repeat: no-repeat;
				width: 33px;
				height: 30px;
				bottom: -10px;
				left: 10%;
				transform: translate(-50%, -50%);

				@media (max-width: @screen-xs) {
					left: -5px;
					bottom: 5px;
				}
			}
			
		}

		&--no-active {
			color: @bg-dark-color;
			position: relative;

			&:before {
				position: absolute;
				content: '';
				background-image: url(/img/ic-check-copy.png);
				background-repeat: no-repeat;
				width: 33px;
			    height: 30px;
			    bottom: 4px;
			    left: 16%;
			    transform: translate(-50%, 50%);

				@media (max-width: @screen-xs) {
					left: 0%;
					bottom: 18px;
				}
			}
			
		}

	}

	&__info {
		flex-basis: 65%;

		@media (max-width: @screen-xs) {
			display: none;
			
		}

	}
  
}
