// Для импорта в диспетчер подключений: @import './src/less/blocks/housing-choice.less';


.housing-choice {
	background: url(/img/main-offers-bg.jpg)no-repeat;
	background-size: cover;
	background-position: 50% 50%;

	&__inner {
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		padding: @padding-std * 5 0px;

	}

	&__title {
		font-family: @font-family-h;
		font-size: @font-size-h3;
		color: @text-white;
		margin-bottom: @margin-bt;
		text-align: center;

	}

	&__item {
		background: none;
		padding: 0;

	}
}
