// Для импорта в диспетчер подключений: @import './src/less/blocks/card-info.less';


.card-info {
	padding: 0 20px;	

	@media 	(min-width: @screen-md) {
		width: 50%;	
	}
		

	&__title {
		font-family: @font-family-h;
		font-size: 	@font-size-h3;
		margin-bottom: 30px;	
	}

	&__text {
		font-size: 	@font-size-p;
		margin-bottom: @margin-card;
	}

	&__list {
		display: flex;	
		flex-direction: column;	

		@media 	(min-width: @screen-xs) {
			flex-direction: row;	
		}
	}
}
