// Для импорта в диспетчер подключений: @import './src/less/blocks/palette.less';


.palette {
	display: flex;	
	flex-direction: column;

	h2 {
	font-size: @font-size-h2;
	font-family: @font-family;
	margin-bottom: @margin-bt;
	}

	&__item {
		margin-bottom: 	@margin-bt;
		display: flex;
	}

	&__subtitle {
		flex-basis: 15%;
	}
}
