// Для импорта в диспетчер подключений: @import './src/less/blocks/banner-rooms.less';


.banner-rooms {
	background: url(/img/rooms-bg-caption.jpg)no-repeat;
	background-size: cover;
	background-position: 50% 50%;

	padding: 70px 0;

	&__inner {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		color: @text-white;
		text-align: center;
		
	}

	&__title {
		font-size: @font-size-h2;
		font-family: @font-family-h;
		margin-bottom: @margin-bt;

	}

	&__text {
		font-family: @font-family-p;
		font-size: @font-size-accent;

		@media (max-width: @screen-xs) {
			display: none;
			
		}

		@media (min-width: @screen-sm) {
			padding: 0 @padding-std *9;
		}
	}
}
