// Для импорта в диспетчер подключений: @import './src/less/blocks/booking-place.less';


.booking-place {
	background:@bg-white;
	max-width: 380px;
	width: 100%;
	margin: auto;
	border: 1px solid @bg-light-grey;
	padding: 30px 0px 45px 0px;
	display: flex;
	justify-content: space-around;
	position: relative;
	


	&__item {
		padding: 0px 76px 80px 66px;
		font-size: @font-size;
		font-family: @font-family-p;
		text-transform: uppercase;
		max-width: @screen-xxs;
		display: flex;
		cursor: pointer;

		@media (max-width: @screen-xs) {
			padding: 0px 30px 80px 30px;
		}


		&:first-child {
			border-right: 1px solid @bg-light-grey;

			@media (max-width: @screen-xs) {
				border: none;
			}
		}


		&--hotel {
		position: relative;
		// padding-right: 40px;
		
		&:before {
			position: absolute;
			content:'';
			background: url(../img/sprite.png)no-repeat;
			background-position: -5px -343px;
			width: 82px;
			height: 64px;
			left: 50%;
		    transform: translateX(-50%);
			top: 30px;
		}

		// &:after {
		// 	position: absolute;
		//     content: '';
		//     width: 1px;
		//     height: 75px;
		//     background: @bg-light-grey;
		//     right: -8px;
		//     top: 40px;
		// }
	}

	&--cottage {
		position: relative;
		
		&:before {
			position: absolute;
			content:'';
			background: url(../img/sprite.png)no-repeat;
			background-position: -5px -271px;
			width: 80px;
			height: 62px;
			left: 50%;
		    transform: translateX(-50%);
			top: 30px;
		}
	}

	&--active {
		position: relative;
		
		&:after {
			position: absolute;
			content: '';
			background-image: url(data:image/svg+xml;base64,);
			background-repeat: no-repeat;
			width: 33px;
		    height: 33px;
		    bottom: -30px;
		    left: 50%;
		    transform: translateX(-50%);
		}

		
	}


	}

