// Для импорта в диспетчер подключений: @import './src/less/blocks/btn.less';


.btn {
	display: inline-block;
	color: @text-white;
	text-transform: uppercase;
	font-family: @font-family-p;
	font-size: @font-size;
	padding: @padding-btn;
	margin: @margin-std;
	letter-spacing: @letter-spacing;
	transition: background .7s ease-out;

	
	&--orange {
		background: @bg-orange;
		border-radius: @radius-btn;

		&:hover {
			background: @bg-orange-hover;
		}
	}
	&--orange-icon {
		background: @bg-orange;
		border-radius: @radius-btn;
		padding-left: 45px;
		position: relative;

		.ic_calendar {
			position: absolute;
			left: 17px;
			
		}

		// &:before {
		// 	position: absolute;
		// 	content: '';
		// 	background: url(/img/icon-calendar.png) no-repeat;
		// 	background-size: contain;
		// 	width: 20px;
		// 	height: 20px;
		// 	top: 11px;
		// 	left: 18px;
		// }

		&:hover {
			background: @bg-orange-hover;
		}
	}

	&--green {
		background: @bg-green;
		border-radius: none;

		&:hover {
			background: @bg-green-hover;
		}
	}

	&--light {
		border-radius: @radius-btn;
		border:2px solid  @text-white;

		&:hover {
			background: @text-white;
			color: @bg-green;
		}
	}

	&--dark {
		color: @text-color;
		border-bottom: 1px dashed @text-color;
		padding: 0;
		
		&:hover {
			border-color: transparent;
			padding: 0;
		}
	}
}
