// Для импорта в диспетчер подключений: @import './src/less/blocks/page-footer.less';


.page-footer {
	border-top:1px solid @border-color;
	padding-bottom: 20px;	

	&__inner {
		display: flex;	
		justify-content: space-around;
		align-items:center;
		max-width: 1200px;
    	margin: auto;
	}

	&__adress {
		font-size: 	@font-size;

		@media 	(max-width: @screen-sm) {
			display: none;
			
		}

		@media 	(min-width: @screen-sm) {
			display: block;
			
		}

	}

	&__logo-and-social {
		display: flex;	
		flex-direction: column;	

	}

	&__contact {

		@media 	(max-width: @screen-sm) {
			display: none;
			
		}

		@media 	(min-width: @screen-sm) {
			display: block;
			
		}
	}

	&__phone {
		font-size: 	@font-size;
		color: @text-color;
	}

	&__email {
		font-size: 	@font-size;
		color: @text-color;
	}
  
}
