// Для импорта в диспетчер подключений: @import './src/less/blocks/cottages-tabs.less';


.cottages-tabs {
	max-width: @screen-sm;
	
	&__caption {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 30px;
		background:@bg-white;
		border-bottom: 1px solid @bg-light-grey;
		border-top: 1px solid @bg-light-grey;
		max-width: 100%;
		margin-bottom: @margin-std2;


		@media (max-width: @screen-xs) {
			flex-direction: column;

		}
	}

	&__item {
		padding: 40px 55px;
		border-right: 1px solid @bg-light-grey;
		text-transform: uppercase;
		color: @bg-grey;
		font-size: @font-size;
		font-family: @font-family-p;
		letter-spacing: @letter-spacing;
		text-align: center;
		margin: 0;
		cursor: pointer;
		

		&:last-child {
			border-right: none;
		}

		@media (max-width: @screen-sm) {
			padding: 40px 30px;
		}

		@media (max-width: @screen-xs) {
			border: none;
			padding: 17px;
		}

		&--active {
			color: @bg-dark-color;
			position: relative;

			&:before {
				position: absolute;
				content: '';
				background-image: url(data:image/svg+xml;base64,);
				background-repeat: no-repeat;
				width: 33px;
				height: 30px;
				bottom: -15px;
				left: 50%;
				transform: translate(-50%, -50%);

				@media (max-width: @screen-xs) {
					left: -5px;
					bottom: 5px;
				}
			}
			
		}

	}

	&__content {
		display: none;
		

		&--visible {
			display: block;
			
		}
	}
  
}
