// Для импорта в диспетчер подключений: @import './src/less/blocks/toggler.less';


.toggler {
	position: absolute;
	z-index: 1;
	top: 30px;
	right: 25px;
	display: flex;
	align-items: center;
	width: 2em;
	height: 2em;
	line-height: 1em;
	user-select: none;

	@media (min-width: @screen-md) {
		display: none;
	}
	

	&__burger  {
	    display: inline-block;
	    vertical-align: bottom;
	    width: 100%;
	    height: .3rem;
	    background: @bg-orange;
	    border-radius: .2rem;
	    position: relative;
	    transition: background .3s .5s;
	    
	    &:before,
	    &:after {
	      width: 100%;
	      height: .3rem;
	      background: @bg-orange;
	      border-radius: .2rem;
	      position: absolute;
	      left: 0;
	      content: '';
	      transform-origin: 50% 50%;
	      transition: top .3s .6s, transform .3s;
	    }
	    
	    &:before {
	      top: .6rem;
	    }
	    
	    &:after {
	      top: -.6rem;
	    }
	  }


	&--close {
    > span {
      transition: background .3s 0;
      background: transparent;
      
      &:before,
      &:after {
        transition: top .3s, transform .3s .5s;
        top: 0;
      }
      
      &:before {
        transform: rotate3d(0, 0, 1, 45deg);
      }
      
      &:after {
        transform: rotate3d(0, 0, 1, -45deg);
      }
    }
  }
  
}
