// Для импорта в диспетчер подключений: @import './src/less/blocks/house-card.less';


.house-card {
	font-family: @font-family-p;
	
	&__inner {
		display: flex;	
		flex-direction: column;	
		position: relative;
		max-width: 	@screen-lg;
		margin: auto;	
			
	}

	&__title {
		font-family: @font-family-h;
		text-align: center;
		margin: @margin-card 0px;

		h1 {
			margin-bottom: 10px;	
		}
			
	}


	&__price {
		font-size: @font-size;
		font-family: @font-family-p;
		text-transform: uppercase;	
		font-weight: 500;

		&--orange {
			color: @bg-orange;
		}
	}


	&__pic {
		margin-bottom: @margin-card;	

		@media 	(min-width: @screen-md) {
			position: absolute;
			top: 18%;
			right: 0;
			width: 50%;	
		}

		img {
			width: 100%;	
		}
	}

	&__btn {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		margin: @margin-card 0px;	
	}

	
}
