// Для импорта в диспетчер подключений: @import './src/less/blocks/points-way.less';


.points-way {
	display: flex;
	justify-content: space-between;
	max-width: @screen-lg;

	@media (max-width: @screen-sm) {
		flex-direction: column;
	}

	&__item {
		font-family: @font-family-p;
		padding: 0px @padding-std * 2;
		text-align: center;

		@media (min-width: @screen-sm) {
			flex-basis: 50%;
		}

	}

	&__title {
		font-size: @font-size;
		text-transform: uppercase;
		margin: auto;
		margin-bottom: @margin-bt / 2;
		padding-top: @padding-std * 4;

		
	}

	&__info {
		font-size: @font-size-p;
		margin-bottom: @margin-bt;

		@media (min-width: @screen-md) {
			text-align: left;
		}
	}
}
