// Для импорта в диспетчер подключений: @import './src/less/blocks/book-info.less';


.booking-info {

	&__inner {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		max-width: @screen-xxs;
		margin: auto;
		margin-bottom: @margin-card;
		padding-top: 75px;

		@media (min-width: @screen-xs) {
			max-width: @screen-xs;
		}
	}

	&__title {
		font-size: @font-size-h2;
		font-family: @font-family-h;
		margin-bottom: @margin-bt - 5px;
	}

	&__text {
		font-size: @font-size-p;
		font-family: @font-family-p;
		text-align: center;
		padding: 0px @padding-std * 5;

		&--orange {
			color: @bg-orange-accent;
			display: inline-block;
			

		}
		

	}
  
}
