.location {
	max-width: @screen-lg;
	margin: auto;

	&__inner {
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		align-items: center;
	}

	&__title {
		font-family: @font-family-h;
		font-size: @font-size-h3;
		margin-bottom: @margin-bt;

