// Для импорта в диспетчер подключений: @import './src/less/blocks/typography.less';


.typography {
	display: flex;	
	flex-direction: column;

	h2 {
	font-size: @font-size-h2;
	font-family: @font-family;
	margin-bottom: @margin-bt;
	}

	&__inner {

	}

	&__item {
		display: flex;	
		justify-content: flex-start;
		align-items: flex-start;
		margin-bottom: 	@margin-bt;

		span {
			margin-right: 100px;	
			flex-basis: 10%;
		}

		h1 {
			font-family: @font-family-h;
			font-size: 	@font-size-h1;
			color: @text-color;
		}

		h2 {
			font-family: @font-family-h;
			font-size: 	@font-size-h2;
			color: @text-color;
		}

		h3 {
			font-family: @font-family;
			font-size: 	@font-size;
			color: @text-color;
			text-transform: uppercase;	
		}

		p {
			font-size: 	@font-size-p;
		}

		&--accent {
			font-size: 	@font-size-accent;
		}

		&--small {
			font-size: 	@font-size;
		}
	}
  
}
