// Для импорта в диспетчер подключений: @import './src/less/blocks/shades.less';


.shades {
	display: flex;	

	&__inner {
		display: flex;	
		flex-wrap: wrap;

	}

	&__item {
		width: 110px;
		height: 96px;	
		margin:0px 20px;	
		display: flex;	
		flex-direction: column;	
		justify-content: flex-end;
		text-align: center;
		border: 1px solid @border-color;
	}

	&__name {
		background-color: @text-white;
		font-size: @font-size-accent - 3;	
		padding: 10px 30px 10px 25px;
	}

	&--dark-color {
		background: @bg-dark-color;
	}

	&--dark-grey {
		background: @bg-dark-grey; 	
	}

	&--grey {
		background: @bg-grey;	
	}

	&--light-grey {
		background: @bg-light-grey;
	}

	&--light-color {
		background: @bg-light-color;	
	}
}
